// Import CWD Cornell Framework variables
@import "cwd-variables";

// normalize.css v7.0.0 | MIT License | github.com/necolas/normalize.css
@import 'normalize';


/* Bootstrap Compatibility Bridge
   ************************************ */

// Box-sizing
*,
*:before,
*:after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
// Screen Reader Only
.sr-only, .visually-hidden, .hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0,0,0,0);
	border: 0;
}
.sr-only-focusable {
	&:active,
	&:focus {
		position: static;
		width: auto;
		height: auto;
		margin: 0;
		overflow: visible;
		clip: auto;
  }
}
.container-fluid {
	margin-right: auto;
	margin-left: auto;
	padding-left: ($grid-gutter-width / 2);
	padding-right: ($grid-gutter-width / 2);
}
.container-fluid:before, .container-fluid:after {
	content: " ";
	display: table;
}
.container-fluid:after {
	clear: both;
}
.row {
	margin-left: (0 - $grid-gutter-width / 2);
	margin-right: (0 - $grid-gutter-width / 2);
}
.row > * {
	padding-left: ($grid-gutter-width / 2);
	padding-right: ($grid-gutter-width / 2);
}
.row:before, .row:after {
	content: " ";
	display: table;
}
.row:after {
	clear: both;
}


/* CWD Cornell Framework Base
   ************************************ */

/* Typography */

body {
	font-family: $cwd-font-family-base;
	font-size: 14px;
	-webkit-font-smoothing: subpixel-antialiased;
}
main, #main, body > footer, .band {
	line-height: 1.6;
}
a {
	color: $cwd-color-link;
}
a:hover, a:focus {
	color: $cwd-color-link-hover;
}
a:active {
	color: $cwd-color-link-active;
}
h1, h2, h3, h4, h5, h6, p, ul, ol, dl, table, .h1, .h2, .h3, .h4, .h5, .h6 {
	margin-top: 0;
}
h1, h2, h3, .h1, .h2, .h3, .serif, .editorial, .editorial p {
  font-family: $cwd-font-family-serif;
}
h4, h5, h6, .h4, .h5, .h6, .sans, .lead, .intro, .page-toc {
  font-family: $cwd-font-family-sans-alt;
  font-weight: normal;
}
h1, h2, h3, .h1, .h2, .h3 {
	font-weight: normal;
	margin-bottom: 0.7em;
	line-height: 1.2;
}
h4, h5, h6, .h4, .h5, .h6 {
	font-weight: normal;
	margin-bottom: 1em;
	line-height: 1.4;
}
h1, .h1 {
	font-size: 36px;
	line-height: 1.1;
}
h2, .h2 {
	font-size: 30px;
}
h3, .h3 {
	font-size: 24px;
}
main h2, #main h2 {
  color: $cwd-color-accent-heading-h2;
}
main h3, #main h3 {
  color: $cwd-color-accent-heading-h3;
}
h4, .h4 {
	font-size: 18px;
	font-weight: 500;
}
h5, .h5 {
	font-size: 16px;
	font-weight: 600;
	color: #444;
}
h6, .h6 {
	font-size: 16px;
	color: $cwd-color-min-contrast;
}
.intro, .lead {
	font-size: 18px;
}
.secondary .intro, .secondary .lead {
	font-size: 16px;
}
.intro strong, .lead strong, .intro b, .lead b {
	font-weight: 700;
}
.editorial {
	font-size: 16px;
	color: #333;
	padding: 4% 4% 3%;
}
p, blockquote, pre, figure, aside, ul, ol, dl, table, #main form {
	margin-bottom: 1.5em;
}
li {
	margin-bottom: 0.5em;
}
li > ul, li > ol {
	margin-top: 0.5em;
}
ol.decimal-leading-zero > li {
	list-style-type: decimal-leading-zero;
}
ol.lower-alpha > li {
	list-style-type: lower-alpha;
}
ol.upper-alpha > li {
	list-style-type: upper-alpha;
}
ol.lower-roman > li {
	list-style-type: lower-roman;
}
ol.upper-roman > li {
	list-style-type: upper-roman;
}
code, kbd, pre, samp {
	font-family: $cwd-font-family-monospace;
}
code, kbd, samp {
	background: rgba(0,0,0,0.07);
	-moz-border-radius: 0;
	-webkit-border-radius: 0;
	border-radius: 0;
}
code {
	color: #b31b1b;
	font-size: 100%;
}
.intro code, .lead code {
	font-size: 90%;
}
mark {
	padding: 0.1em 0.2em;
}
small, figcaption, .caption, h5 small, h6 small, .h5 small, .h6 small {
	font-size: 85.714285714286%; /* 12px base */
}
small figcaption, figcaption small, small .caption, .caption small {
	font-size: 91.666666666667%; /* 11px base */
}
hr {
	border: none;
	border-bottom: 1px solid #e5e5e5;
	margin-bottom: 1.25em;
	clear: both;
	padding: 1px 0; // this makes 1px HRs easier to select in browser inspectors and WYSIWYG editors!
}
hr.inline {
	clear: none;
}
hr.clear, hr.section-break, hr.section-header, hr.section-footer {
	clear: both;
	float: left;
	width: 100% !important;
}
hr.section-break {
	margin: 1.5em 0 2.25em;
}
hr.section-header {
	margin: 0 0 2.25em;
}
hr.section-footer {
	margin: 1.5em 0 0;
}
hr.fade {
	opacity: 0.3;
}
h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child, p:last-child, ul:last-child, ol:last-child, dl:last-child, li:last-child, table:last-child, figure:last-child, aside:last-child, #main-content form:last-child, .panel:last-child, .row:last-child {
	margin-bottom: 0;
}


/* Utility */

.vertical-align {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-webkit-align-items: center;
	-moz-box-align: center;
	-ms-flex-align: center;
	align-items: center;
}
.flex {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}
.flex-grow {
	-webkit-flex-wrap: nowrap;
	-ms-flex-wrap: nowrap;
	flex-wrap: nowrap;
}
.flex-grow > * {
	-webkit-box-flex: 1;
	-webkit-flex-grow: 1;
	-moz-box-flex: 1;
	-ms-flex-positive: 1;
	flex-grow: 1;
}
.stunt-double { /* This bold and fearless daredevil is used as a second target for multiple backgrounds and other tricks */
	background: none; // Born of a shadowy past...
	pointer-events: none; // Impossible to pin down...
	position: absolute; // Unshakably resolute...
	top: 0; // ...and top of his .class at the academy!
	height: 100%; // Tall in stature...
	width: 100%; // ...wide of vision!
}
.stunt-double .stunt-double { // .stunt-quadrupal?

}
.icon-fa, .list-menu.nav a:after, .cwd-component .link.link-button-icon a:before, .header-emblem:before, .back-to-toc:before {
	font: normal normal normal 14px/1 FontAwesome;
	speak: none;
	font-size: inherit;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.icon-zmdi, .tutorial.tip:before, .tutorial.note:before, .breadcrumb li a:after {
	display: inline-block;
	font: normal normal normal 14px/1 'Material-Design-Iconic-Font';
	font-size: inherit;
	speak: none;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.round {
	-moz-border-radius: 100em;
	-webkit-border-radius: 100em;
	border-radius: 100em;
}
.curved {
	-moz-border-radius: 6px;
	-webkit-border-radius: 6px;
	border-radius: 6px;
}
.squared {
	-moz-border-radius: 0 !important;
	-webkit-border-radius: 0 !important;
	border-radius: 0 !important;
}
.framed {
	border: 4px solid #fff;
}
.text-left {
	text-align: left;
}
.text-right {
	text-align: right;
}
.text-center {
	text-align: center;
}
.text-justify {
	text-align: justify;
}
.text-nowrap {
	white-space: nowrap;
}
.text-lowercase {
	text-transform: lowercase;
}
.text-uppercase, .uppercase {
	text-transform: uppercase;
}
.text-capitalize {
	text-transform: capitalize;
}
.puretext {
	text-transform: none !important;
	color: inherit !important;
}
.no-border {
	border: 0 !important;
}
.no-margin {
	margin-bottom: 0 !important;
}
.low-margin {
	margin-bottom: 0.15em !important;
}
.half-margin {
	margin-bottom: 0.5em !important;
}
.nobr {
	white-space: nowrap;
}
.tint {
	background: #f7f7f7;
}
.white {
	background: #fff;
}
.smallprint {
	color: #666;
	font-size: 13px;
	line-height: 1.6;
}
.fade {
	opacity: 0.4;
}
.fade-bw, .fade-bw code {
	color: #9a9a9a !important;
}
.invisible {
	visibility: hidden;
}
.relative {
	position: relative;
}
.clear {
	clear: both;
}
.full-clear {
	clear: both;
	float: left;
	width: 100%;
}
.no-clear {
	clear: none !important;
}
a.inherit {
	color: inherit;
	text-decoration: none;
	border: 0;
}
.dropshadow {
	-moz-box-shadow: 0 1px 2px rgba(0, 0, 0, .15);
	-webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, .15);
	box-shadow: 0 1px 2px rgba(0, 0, 0, .15);
}
.dropshadow-dark {
	-moz-box-shadow: 0 2px 3px rgba(0, 0, 0, .3);
	-webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, .3);
	box-shadow: 0 2px 3px rgba(0, 0, 0, .3);
}
.dropshadow-diffuse {
	-moz-box-shadow: 0 1px 24px rgba(0, 0, 0, .15);
	-webkit-box-shadow: 0 1px 24px rgba(0, 0, 0, .15);
	box-shadow: 0 1px 24px rgba(0, 0, 0, .15);
}
.header-emblem:before {
	content: '\f02e';
	padding-right: 0.4em;
	color: $cwd-color-subheading-text;
}
.receded {
	background: #87c5f0;
	display: block;
	overflow: hidden;
}
.receded img {
	vertical-align: middle;
	-webkit-filter: grayscale(0.6) brightness(120%);
	-moz-filter: grayscale(0.6) brightness(120%);
	-o-filter: grayscale(0.6) brightness(120%);
	-ms-filter: grayscale(0.6) brightness(120%);
	filter: grayscale(0.6) brightness(120%);
	opacity: 0.7;

	transition: all .15s;
	-webkit-transition: all .15s;
	-moz-transition: all .15s;
	-o-transition: all .15s;
	-ms-transition: all .15s;
}
.ios .receded img, .android .receded img,
a.receded:hover img, a.receded:focus img,
a:hover .receded img, a:focus .receded img {
	-webkit-filter: grayscale(0) brightness(100%);
	-moz-filter: grayscale(0) brightness(100%);
	-o-filter: grayscale(0) brightness(100%);
	-ms-filter: grayscale(0) brightness(100%);
	filter: grayscale(0) brightness(100%);
	opacity: 1;
}
disabled { // custom tag for disabling large regions of HTML (during development)
	//display: none;
}

/* Custom Links */

.no-deco, .link-icon, .link-icon-right {
	text-decoration: none;
}
.link-icon .fa, .link-icon .zmdi, .link-icon .icon, .deco .fa, .deco .zmdi, .deco .icon {
	padding-right: 0.35em;
}
.link-icon-right .fa, .link-icon-right .zmdi, .link-icon-right .icon {
	padding-left: 0.35em;
}
.link-block {
	background: rgba(0,0,0,0.07);
	padding: 0.5em 0.8em 0.4em 0.7em;
	display: inline-block;
	line-height: 1;
}
a.link-more {
	display: inline-block;
	line-height: 1;
	text-decoration: none;
	white-space: nowrap;
}
a.link-button {
	font-family: $cwd-font-family-sans-alt;
	background: $cwd-color-button;
	font-weight: 400;
	font-size: 16px;
	padding: 0.5em 0.8em;
	display: inline-block;
	line-height: 1;
	color: #fff;
	text-decoration: none;
	white-space: nowrap;
}
a.link-button:hover, a.link-button:focus {
	background: $cwd-color-button-hover;
}
a.link-button:active {
	background: $cwd-color-button-active;
}
a.link-button .fa {
	font-size: 18px;
	position: relative;
	top: 0.5px;
	margin: 0 -0.05em 0 0.5em;
}
a.link-more .fa {
	position: relative;
	top: 0.5px;
	margin: 0 -0.05em 0 0.5em;
}
a.link-button .fa:first-child, a.link-more .fa:first-child {
	margin: 0 0.5em 0 -0.05em;
}


/* Accessibility */

#skipnav {
	position: absolute;
	top: 10px;
	left: 15px;
	z-index: 5;
}
#skipnav a {
	color: #fff;
	padding: 2px 8px;
	text-decoration: none;
	white-space: nowrap;
	position: absolute;
	left: -1000em;
}
#skipnav a:focus {
	background: #2e5690;
	left: 0;
}
.aria-target:focus {
	outline: none;
}


/* Site Footer */

body > footer {
	position: relative;
	z-index: 2;
	background: #2a2a2a;
	color: #bbb;
	padding: 0;
	font-size: 13px;
	text-align: center;
}
body > footer .sub-footer {
	background: #212121;
	font-size: 13px;
}
body > footer .footer-links li {
	line-height: 1.4;
}
body > footer .footer-links a {
	text-decoration: none;
}
body > footer .footer-links a:hover, body > footer .footer-links a:focus {
	text-decoration: underline;
}
body > footer a {
	color: #fff;
	-moz-text-decoration-color: rgba(255,255,255,0.4);
	-webkit-text-decoration-color: rgba(255,255,255,0.4);
	text-decoration-color: rgba(255,255,255,0.4);
}
body > footer a:hover, body > footer a:focus {
	color: #fff;
	-moz-text-decoration-color: rgba(255,255,255,1);
	-webkit-text-decoration-color: rgba(255,255,255,1);
	text-decoration-color: rgba(255,255,255,1);
}
body > footer a:active {
	color: #666;
	-moz-text-decoration-color: rgba(255,255,255,0.4);
	-webkit-text-decoration-color: rgba(255,255,255,0.4);
	text-decoration-color: rgba(255,255,255,0.4);
}
body > footer .footer-links a {
	color: #7bc9ff;
	-moz-text-decoration-color: rgba(123,201,255,0.4);
	-webkit-text-decoration-color: rgba(123,201,255,0.4);
	text-decoration-color: rgba(123,201,255,0.4);
	transition: color 0.2s, -moz-text-decoration-color 0.2s, -webkit-text-decoration-color 0.2s, text-decoration-color 0.2s;
}
body > footer .footer-links a:hover, body > footer .footer-links a:focus {
	color: #fff;
	-moz-text-decoration-color: rgba(255,255,255,1);
	-webkit-text-decoration-color: rgba(255,255,255,1);
	text-decoration-color: rgba(255,255,255,1);
	transition: none;
}
body > footer .footer-links a:active {
	color: #666;
	-moz-text-decoration-color: rgba(255,255,255,0.4);
	-webkit-text-decoration-color: rgba(255,255,255,0.4);
	text-decoration-color: rgba(255,255,255,0.4);
}
body > footer a.link-block {
	background: rgba(109,195,255,0.15);
	text-decoration: none;
	font-family: $cwd-font-family-sans-alt;
	font-size: 28px;
	padding: 0.5em 0.8em;
	margin-top: 8px;
}
body > footer a.link-block:hover, body > footer a.link-block:focus {
	background: rgba(55,160,233,0.4);
}
body > footer a.link-block:active {
	background: rgba(55,160,233,0.6);
	color: #fff;
}
body > footer .list-menu {
	max-width: 480px;
	max-width: 80vw;
	margin: 0 auto;
	float: none;
}
body > footer .list-menu.vertical li {
	padding: 0.7em 0;
	border-top: 1px solid #4c4c4c;
}
body > footer .main-footer {
	padding: 45px 0;
}
body > footer .sub-footer {
	padding: 20px 0;
}
body > footer .h3 {
	color: #eee;
}
body > footer .h5 {
	color: #eee;
	font-size: 14px;
	margin-top: 0.3em;
}
body > footer .h6 {
	color: #eee;
	font-size: 14px;
	text-transform: uppercase;
}
body > footer ul li {
	font-size: 13px;
}
.footer-links {
	margin-bottom: 0;
}
.footer-links > * {
	float: left;
	width: 100%;
	margin: 2em 0 2em;
}
body > footer .sub-footer .primary {
	margin-bottom: 20px;
}
body > footer .sub-footer .list-menu {
	padding-bottom: 20px;
	border-bottom: 1px solid #3c3c3c;
}
body > footer .sub-footer .list-menu li {
	float: none;
	display: inline;
	padding: 0;
	margin: 0.2em 0.6em;
}

/* Footer Social Media */

body > footer .social {
	line-height: 0;
	display: flex;
	justify-content: center;
	max-width: 380px;
	margin: 30px auto 0;
}
body > footer .social > a {
	padding: 2px 8px;
}
body > footer .social img {
	opacity: 0.7;
	width: 48px;
	height: auto;
	max-width: 100%;
}
body > footer .social > a:first-of-type {
	padding-left: 0;
}
body > footer .social > a:last-of-type {
	padding-right: 0;
}
body > footer .social img:hover, body > footer .social img:focus {
	opacity: 1;
}


/* Section Footers and Asides */

article footer {
	background: #f7f7f7;
	color: #666;
	padding: 20px;
	overflow: hidden;
	clear: both;
	font-size: 13px;
}
article aside {
	overflow: hidden;
	background: #e3f1f6;
	padding: 20px;
}
article aside.column {
	background: transparent;
	padding: 0 0 20px;
	border-bottom: 2px solid #ebebeb;
}

/* Blockquotes */
blockquote {
	margin: 0 0 2em;
	padding: 3px 0 3px 20px;
	border-left: 1px solid #e6e6e6;
}

blockquote.offset {
	font-family: $cwd-font-family-serif-alt;
	font-size: 20px;
	line-height: 1.45;
	color: #444;
	background: #fff;
	border: 1px solid #e6e6e6;
	border-left-color: $cwd-color-accent-heading-h2;
	//border-right-color: $cwd-color-accent-heading-h2;
	margin: 1.25em 0;
	padding: 15px 30px;
}
blockquote.offset p {
	font-size: 20px;
	line-height: 1.45;
}
blockquote footer, blockquote.offset footer {
	background: none;
	padding: 0;
}

/* Figures */

figure {
	background: rgba(0,0,0,0.05);
	clear: both;
	margin: 1em 0;
}
.caption {
	background: rgba(0,0,0,0.05);
	clear: both;
	margin: 0 0 1em;
}
a figure:last-child:after {
	content: '';
}
.secondary figure {
	margin: 0;
	background: #fff;
	-moz-box-shadow: 0 2px 8px rgba(0, 0, 0, .05);
	-webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, .05);
	box-shadow: 0 2px 8px rgba(0, 0, 0, .05);
}
figure img {
	display: block;
	width: 100%;
	height: auto;
}
figcaption, .caption {
	padding: 0.55em 0.6em;
	color: #555;
	clear: both;
}
a figcaption {
	text-decoration: none;
}



/* Layout */

// #site-header {
// 	background: #f6f6f6;
// 	min-height: 400px; // TEMP
// }
main, #main {
  padding-top: 2.5em;
  padding-bottom: 2.5em;
  background: #fff;
}
article section {
	float: left;
	clear: both;
	width: 100%;
	padding: 0 0 3em;
	margin-bottom: 2.5em;
	border-bottom: 1px solid #ebebeb;
}
article section.no-border {
	padding: 0 0 1.5em;
	margin-bottom: 1em;
}
article section:last-child, article section.no-border:last-child {
	padding-bottom: 0;
	margin-bottom: 0;
	border-bottom: 0;
}
.row > .primary, .row > .secondary {
	float: left;
	width: 100%;
	margin-bottom: 2.5em;
}
main .row > .primary:after, main .row > .secondary:after {
	content: '';
	display: block;
	float: left;
	width: 100%;
	min-height: 1px;
	margin-top: 3em;
	border-bottom: 1px solid #ebebeb;
	clear: both;
}
main .row > .primary:last-child:after, main .row > .secondary:last-child:after {
	border-bottom: 0;
}
.row > .secondary:last-child {
	margin-bottom: 0;
}
.band, body > footer, body > header {
	float: left;
	width: 100%;
}
.band.padded {
	padding: 2.5em 0;
}
.band.fullwidth > .container-fluid {
	max-width: none;
}
.band.accent1 {
	background: #e8f3f8;
}
.row > .secondary:empty {
	display: none;
}
#main-content {
	background: #fff;
}
.content-block {
	float: left;
	width: 100%;
	margin: 0 0 2em;
}
.content-block + .content-block, .secondary-navigation + .content-block {
	border-top: 1px solid #ebebeb;
	padding-top: 1.75em;
}


/* Tables */

.table {
	font-size: 13px;
	width: 100%;
	margin-bottom: 3em;
	border-collapse: collapse;
}
.table th[scope='col'], .table thead th {
	text-align: left;
}
.table th[scope='col'].text-center, .table thead th.text-center {
	text-align: center;
}
.table caption {
	padding: 0.6em 0;
	border-top: 2px solid #e8e8e8;
	font-family: $cwd-font-family-sans-alt;
	font-size: 16px;
	font-weight: 400;
}
.table .blank {
	background: none !important;
}
.table td, .table th {
	background: #fff;
	padding: 0.4em 0.6em;
	border: 1px solid #d9d9d9;
	border-width: 0 1px;
}
.table.compact td, .table.compact th {
	font-size: 12px;
	padding: 0.2em 0.3em;
}
.table td:first-child, .table th:first-child {
	border-left-width: 0;
}
.table td:last-child, .table th:last-child {
	border-right-width: 0;
}
.table th {
	background: #fafafa;
}
.table thead th {
	background: #efefef;
	border-bottom-width: 1px;
}

.table.bordered tr > * {
	border-width: 1px;
}

.table.flat tr > * {
	border-width: 0;
}
.table.flat td, .table.flat th {
	padding: 0.8em;
	background: #fafafa;
}
.table.flat th {
	background: #f2f2f2;
}
.table.flat th[scope='col'], .table.flat thead th {
	background: #e6e6e6;
}

.table.striped tr:nth-child(even) td {
	background: #f7f7f7;
}
.table.striped tbody tr:nth-child(even) th {
	background: #f0f0f0;
}
.table.flat.striped tr:nth-child(even) td {
	background: #ededed;
}
.table.flat.striped tbody tr:nth-child(even) th {
	background: #e8e8e8;
}

.table.colored td {
	background: #f6ffff;
	border-color: #afbfc9;
}
.table.colored.striped tr:nth-child(even) > td {
	background: #ebf3f8;
}
.table.colored th[scope='col'], .table.colored thead th, 
.table.flat.colored th[scope='col'], .table.flat.colored thead th {
	background: #cfe3ef;
	border-color: #afbfc9;
}
.table.flat.colored tr > th[scope='row'] {
	border-right-width: 1px;
}

.table.rainbow tr * {
	border-color: #aaa;
}
.table.rainbow tr > *:nth-child(5n+1) {
	background: #f6ffff;
}
.table.rainbow tr > *:nth-child(5n+2) {
	background: #f6fff7;
}
.table.rainbow tr > *:nth-child(5n+3) {
	background: #fffff6;
}
.table.rainbow tr > *:nth-child(5n+4) {
	background: #fff7fa;
}
.table.rainbow tr > *:nth-child(5n+5) {
	background: #fcf7ff;
}
.table.rainbow thead tr > *:nth-child(5n+1) {
	background: #cfe3ef;
}
.table.rainbow thead tr > *:nth-child(5n+2) {
	background: #cfefd1;
}
.table.rainbow thead tr > *:nth-child(5n+3) {
	background: #efe7cf;
}
.table.rainbow thead tr > *:nth-child(5n+4) {
	background: #efd3d1;
}
.table.rainbow thead tr > *:nth-child(5n+5) {
	background: #e7d1f1;
}
.table.rainbow.striped tr:nth-child(even) > td:nth-child(5n+1) {
	background: #ebf3f8;
}
.table.rainbow.striped tr:nth-child(even) > td:nth-child(5n+2) {
	background: #ebf8ec;
}
.table.rainbow.striped tr:nth-child(even) > td:nth-child(5n+3) {
	background: #f8f5eb;
}
.table.rainbow.striped tr:nth-child(even) > td:nth-child(5n+4) {
	background: #f8edec;
}
.table.rainbow.striped tr:nth-child(even) > td:nth-child(5n+5) {
	background: #f5ecf9;
}
.table.colored tr > th[scope='row'],
.table.rainbow tr > th[scope='row'] {
	background: #fafafa !important;
}
.table.colored.striped tr:nth-child(even) > th[scope='row'], .table.flat.colored .blank, 
.table.rainbow.striped tr:nth-child(even) > th[scope='row'], .table.flat.rainbow .blank, .table.rainbow thead th:not([scope='col']) {
	background: #f0f0f0 !important;
}

.table.flat.sectioned thead th {
	border-bottom-width: 1px;
}
.table.flat.sectioned tbody > tr:first-child > td {
	border-top: 1px solid #d9d9d9;
}
.table.flat.sectioned tbody > tr:first-child > th {
	border-top: 1px solid #d9d9d9;
}
.table.sectioned tbody:before {
  content: '';
  display: block;
  height: 1.5em;
}

/* Forms */
#main form .form-item, #main form .form-actions {
	margin-bottom: 1.5em;
}
#main form label {
	font-family: $cwd-font-family-sans-alt-semibold;
	font-size: 15px;
	font-weight: 600;
	display: block;
	margin: 0 0 0.15em;
	color: #333;
	max-width: 600px;
}
#main form input {
	font-size: 16px;
	margin: 0 0 0.25em;
	max-width: 600px;
}
#main form input[type='text'], 
#main form input[type='password'], 
#main form input[type='email'], 
#main form input[type='url'], 
#main form input[type='search'] {
	width: 100%;
	padding: 0.4em 0.5em;
}
#main form .description {
	font-size: 13px;
	color: $cwd-color-min-contrast;
}
#main form input[type='submit'], 
#main form input[type='reset'], 
#main form input[type='button'] {
	-webkit-appearance: none;
	background: #767676;
	color: #fff;
	border: 0;
	padding: 0.5em 1.5em;
	font-family: $cwd-font-family-sans-ui;
	cursor: pointer;
	transition: background .1s;
}
#main form input[type='reset']:hover, #main form input[type='reset']:focus, 
#main form input[type='button']:hover, #main form input[type='button']:focus {
	background: #888;
}
#main form input[type='reset']:active, 
#main form input[type='button']:active {
	background: #666;
}
#main form input[type='submit'] {
	background: $cwd-color-button;
}
#main form input[type='submit']:hover, #main form input[type='submit']:focus {
	background: $cwd-color-button-hover;
}
#main form input[type='submit']:active {
	background: $cwd-color-button-active;
}



/* Styled Lists */

.list-menu, .list-menu > ul, .list-menu .menu-block-wrapper > ul {
	padding: 0;
	margin: 0 0 1.5em;
	list-style: none;
	float: left;
	width: 100%;
	clear: both;
}
.list-menu + h2, .list-menu + h3, .list-menu + h4, .list-menu + h5, .list-menu + h6 {
	clear: left;
}
.list-menu:last-child, .list-menu > ul:last-child, .list-menu .menu-block-wrapper > ul:last-child {
	margin-bottom: 0;
}
.list-menu li {
	float: left;
	display: block;
	background: none !important;
	padding: 0 0.6em 0 0;
	margin: 0 0.6em 0.6em 0;
	border-width: 0 1px 0 0;
}
.list-menu li:last-child {
	border-right-width: 0;
}
.list-menu.vertical li {
	float: none;
	padding: 0.4em 0;
	margin: 0;
	border-width: 0 0 1px 0;
}
.list-menu.vertical li:first-child {
	border-top-width: 1px;
}
.list-menu.divs li {
	border-color: #e5e5e5;
	border-style: solid;
}
.list-menu.links li {
	padding: 0;
	margin: 0 2px 2px 0;
}
.list-menu.links.vertical li {
	margin-right: 0;
}
.list-menu.links a {
	display: block;
	background: #eee;
	background: rgba(0,0,0,0.04);
	padding: 0.4em 0.7em;
}
.list-menu.links a:hover, .list-menu.links a:focus {
	background: rgba(0,0,0,0.07);
	color: #000;
}
.list-menu.links a:active {
	background: rgba(0,0,0,0.12);
}
ul.list-menu.links.vertical ul, .list-menu.links.vertical ul ul {
	margin-top: 2px;
}
.list-menu li.icon:before {
	padding-right: 6px;
}
.list-menu.cols-2 li, .list-menu.cols-3 li, .list-menu.cols-4 li {
	width: 100%;
}


/* Main Navigation
   ************************************ */

#main-navigation {
	background: #666;
	color: #fff;
}
#main-navigation li {
	margin: 0 1px 0 0;
	font-family: $cwd-font-family-sans-ui;
	font-size: 15px;
	line-height: 18px;
}
#main-navigation a {
	color: #fff;
	padding: 14px 1.5em;
	text-decoration: none;
	background: transparent;

	-webkit-transition: background-color .15s;
	-moz-transition: background-color .15s;
	-o-transition: background-color .15s;
	-ms-transition: background-color .15s;
	transition: background-color .15s;
}
#main-navigation li.current-menu-item > a, #main-navigation li.current-menu-ancestor > a, #main-navigation li.active-trail > a, 
#main-navigation a:hover, #main-navigation li.active > a, #main-navigation > .container-fluid > ul > li > a.is-active, #main-navigation li:hover > a, .dropdown-menu li:hover > a { /* includes WordPress and Drupal menu classes */
	background: #444;
}
#main-navigation a:focus {
	background: #444;
	/* outline-color: #fff; */
}

/* Dropdown Navigation
   ************************************ */

.dropdown-menu ul ul {
	position: absolute;
	z-index: 100;
	background: #555;
	width: auto;
	min-width: 12em;
	max-width: 24em;
	margin: 0 !important;
	overflow: hidden;
	height: 0;
	opacity: 0;
	-ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=0)';

	-webkit-transition: opacity 0s linear .2s;
	-moz-transition: opacity 0s linear .2s;
	-o-transition: opacity 0s linear .2s;
	-ms-transition: opacity 0s linear .2s;
	transition: opacity 0s linear .2s;
}
.dropdown-menu ul ul.flip {
	right: 0;
}
.dropdown-menu ul ul > li, #main-navigation.dropdown-menu ul ul > li {
	margin: 0;
}
.dropdown-menu ul ul > li:first-child {
	border-top: 1px solid #888;
}
.dropdown-menu a .fa {
	margin-left: 0.5em;
}
.dropdown-menu li.parent:hover > ul, .dropdown-menu li.parent.open > ul {
	overflow: visible;
	height: auto;
	opacity: 1;
	-ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=100)';
}
.dropdown-menu ul ul a {
	-webkit-transition: background-color 0s;
	-moz-transition: background-color 0s;
	-o-transition: background-color 0s;
	-ms-transition: background-color 0s;
	transition: background-color 0s;
}

/* submenus */
.dropdown-menu ul ul ul {
	top: 0;
	left: 100%;
	border-left: 1px solid #888;
	-moz-box-shadow: 0 -2px 0 #888;
	-webkit-box-shadow: 0 -2px 0 #888;
	box-shadow: 0 -2px 0 #888;
}
.dropdown-menu ul ul ul.flip {
	left: auto;
	right: 100%;
	border-left: 0;
	border-right: 1px solid #888;
}
/*
.dropdown-menu ul ul ul.flip, .dropdown-menu ul ul.flip ul {
	-moz-box-shadow: 0 -2px 0 #888;
	-webkit-box-shadow: 0 -2px 0 #888;
	box-shadow: 0 -2px 0 #888;
}
 */
.dropdown-menu ul ul ul > li:first-child {
	border-top-width: 0;
}
.dropdown-menu li.parent {
	position: relative;
}
.dropdown-menu li.parent li a {
	white-space: nowrap; /* disable if necessary to display long link titles with line breaks */
	overflow: hidden;
	text-overflow: ellipsis;
}
.dropdown-menu li.parent li.menu-section {
	border-top: 2px solid rgba(255,255,255,0.15);
}
#mobile-close, #mobile-nav-dimmer {
	display: none;
}
#mobile-home {
	position: absolute;
	left: -10000em;
	top: auto;
	width: 1px;
	height: 1px;
	overflow: hidden;
}

@media (max-width: ($screen-md-min - 1)) { // Mobile Main Navigation ****** //
// Note: if changing this breakpoint, be sure to also update the associated value in cwd_utilities.js

#mobile-home {
	position: static;
	width: auto;
	height: auto;
}
#main-navigation, .home #main-navigation {
	position: absolute;
	top: 0;
	z-index: 1000;
	display: none;
	width: 100%;
	background: none;
}
#main-navigation.open, .home #main-navigation.open {
	display: block;
}
#main-navigation li {
	width: 100%;
}
#mobile-nav-dimmer {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background: #333;
	background: rgba(51,51,51,0.95);
	z-index: 999;
	display: none;
}
#mobile-home, #mobile-close {
	display: inline-block;
}
#mobile-home:before, #mobile-close:before {
	display: inline-block;
	font: normal normal normal 14px/1 'Material-Design-Iconic-Font';
	font-size: inherit;
	speak: none;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	content: '\f175';
	font-size: 24px;
}
#mobile-close {
	float: right;
}
#mobile-close:before {
	content: '\f135';
}
#main-navigation ul {
	border-top: 1px solid #999;
}
#main-navigation ul ul, .dropdown-menu ul ul {
	background: #222;
	background: rgba(0,0,0,0.3);
	width: 100%;
	border-top: 0;
	position: static;
	min-width: 0 !important;
	max-width: none !important;
	display: none;
	opacity: 1;
	height: auto;
}
#main-navigation li.current-menu-item a, #main-navigation li.current-menu-ancestor a, 
#main-navigation li.active-trail a, 
#main-navigation li.active a, 
#main-navigation li > a.is-active {
	background: inherit; /* disable active link highlight */
}
#main-navigation .fa:before, .dropdown-menu .fa:before {
	content: '\f078';
}
.dropdown-menu .fa-navicon:before {
	content: '\f0c9';
}
#main-navigation li.open > a {
	background: #444;
}
#main-navigation .open .fa {
	background: #333;
}
#main-navigation ul ul .fa, .dropdown-menu ul ul .fa {
	display: none;
}
#main-navigation .open .fa:before, .dropdown-menu .open .fa:before {
	content: '\f078';
}
#main-navigation .open > a .fa:before, .dropdown-menu .open > a .fa:before {
	content: '\f077';
}
#main-navigation .open > ul, .dropdown-menu .open > ul {
	display: block;
}
#main-navigation li {
	border-bottom: 1px solid #999;
}
#main-navigation ul ul > li {
	border-bottom: 1px solid #666;
}
#main-navigation ul ul > li:first-child {
	border-top: 0;
}
#main-navigation ul ul > li:last-child {
	border-bottom: 1px solid #444;
}
#main-navigation li a {
	padding: 0 1.2em 0 1.5em;
	line-height: 3.6em;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}
#main-navigation li.parent > a span:first-child {
	float: left;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	max-width: 77%;
	max-width: calc(100% - 4.3em);
}
#main-navigation li li.parent > a span:first-child {
	display: inline;
}
#main-navigation li.active > a, #main-navigation li > a.is-active, #main-navigation li.active:hover > a, #main-navigation li:hover > a.is-active {
	background: #0068ac;
	background: rgba(0,104,172,0.5);
}
#main-navigation li:hover > a {
	background: #555;
}
#main-navigation ul ul li:hover > a {
	background: #333;
}
#main-navigation .fa {
	float: right;
	line-height: 3.6em;
	width: 3.8em;
	margin-right: -1.2em;
	text-align: center;
	background: #555;
	border-left: 1px solid #777;
}
#main-navigation .fa:hover {
	background: #444;
}
#mobile-nav {
	display: block;
}

} // ************** //


