@media (min-width: $screen-lg-min) { // LARGE GRID ************************ //


.editorial {
	font-size: 17px;
}

.cwd-card-grid .card, .cwd-card-grid .card:nth-child(2n+2) {
	width: 31%;
	margin: 0 3.5% 30px 0;
	border: 0;
}
.cwd-card-grid .card:nth-child(1n+3) {
	border-top: 0;
	padding-top: 0;
}
.cwd-card-grid .card:nth-child(3n+3) {
	margin-right: 0;
}
.cwd-card-grid .card:nth-child(3n+1) {
	clear: left;
}
.cwd-card-grid .card:nth-child(2n+1) {
	clear: none;
}
.cwd-card-grid .card:nth-child(1n+4) {
	border-top: 3px solid #e7e7e7;
	padding-top: 30px;
}

#main-navigation .nav-justified > li a {
	padding-left: 2.5em;
	padding-right: 2.5em;
}

body > footer .main-footer .primary {
	padding-left: 5%;
}

/* Blockquote */
blockquote.offset {
	font-size: 23px;
	line-height: 1.55;
	margin: 4px $grid-gutter-width 12px #{-$grid-gutter-width*2};
}
.sidebar-left blockquote.offset {
	margin: 4px #{-$grid-gutter-width} 12px $grid-gutter-width;
}
blockquote.offset p {
	font-size: 23px;
	line-height: 1.55;
}

} // ************** //
// ************************************************************************ //
@media (min-width: $screen-xl-min) { // EXTRA-LARGE GRID ****************** //

blockquote.offset {
	margin: 4px $grid-gutter-width 12px #{-$grid-gutter-width*3};
}
.sidebar-left blockquote.offset {
	margin: 4px #{-$grid-gutter-width} 12px $grid-gutter-width;
}


} // ************** //
// ************************************************************************ //
@media (max-width: 767px) { // Prevent iPhone Safari Form Zoom ************ //

select, textarea, input[type='text'], input[type='password'], input[type='datetime'], input[type='datetime-local'], input[type='date'], input[type='month'], input[type='time'], input[type='week'], input[type='number'], input[type='email'], input[type='url'] {
	font-size: 16px;
}
.no-mobile {
	display: none !important;
}
	
} // ************** //
/* ********************************************************** */
@media only print { /* Project Print Styles ***************** */


.back-to-toc {
	display: none !important;
}
table td, table th {
	border: 1px solid #ddd !important;
}


} // ************** //




